import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: () => import(/* 首页: "/" */ '@/components/Index.vue')
    },
    {
      path: '/user',
      name: 'User',
      component: () => import(/* 用户管理: "user" */ '@/components/User.vue')
    },
    {
      path: '/log',
      name: 'Log',
      component: () => import(/* 日志: "log" */'@/components/Log.vue')
    },
    {
      path: '/carManagement',
      name: 'CarManagement',
      component: () => import(/* 汽车管理: "carManagement" */'@/components/CarManagement.vue')
    },
    {
      path: '/carInfo',
      name: 'CarInfo',
      component: () => import(/* 汽车信息: "carInfo" */'@/components/CarInfo.vue')
    },
    {
      path: '/newsList',
      name: 'NewsList',
      component: () => import(/* 消息列表: "newsList" */'@/components/NewsList.vue')
    },
    {
      path: '/passAnnal',
      name: 'PassAnnal',
      component: () => import(/* 通行记录: "passAnnal" */'@/components/PassAnnal.vue')
    },
    {
      path: '/passAnnal',
      name: 'PassAnnal',
      component: () => import(/* 通行记录: "passAnnal" */'@/components/PassAnnal.vue')
    },
    {
      path: '/inspectionAnnal',
      name: 'InspectionAnnal',
      component: () => import(/* 检查记录: "inspectionAnnal" */'@/components/InspectionAnnal.vue')
    },
    {
      path: '/delCarList',
      name: 'DelCarList',
      component: () => import(/* 已删除车辆列表: "delCarList" */'@/components/DelCarList.vue')
    },
    
  ]
})



